<template lang="pug">

.page-profile
	.profile
		.nav
			router-link.nav-item(v-for="(item, index) in nav" :to="item.url" :key="index") {{item.name}}
			.nav-bg(:style="{left: activePage * 25 + '%'}")

		router-view

</template>

<script>
export default {
	data(){
		return {
			nav: [
				{
					name: 'Бонусы',
					url: '/profile/'
				},
				{
					name: 'История покупок',
					url: '/profile/history/'
				},
				{
					name: 'Персональные данные',
					url: '/profile/personal/'
				},
				{
					name: 'Настройки',
					url: '/profile/settings/'
				}
			],
			left: null
		}
	},
	computed: {
		activePage(){
			let path = this.$router.app._route.path
			return this.nav.findIndex(item => (item.url == path) || (item.url == path + '/'))
		}
	}
}
</script>

<style lang="stylus" scoped>

.profile
	width 1080px
	margin 50px auto
.nav
	background-color #fff
	border-radius 10px
	height 38px
	padding 5px 6px
	left 50%
	position relative
	display inline-flex
	transform translateX(-50%)
	margin-bottom 30px
	box-shadow 0 0 6px 0 rgba(#000, .1)
	&-item
		align-items center
		display flex
		justify-content center
		font-size 14px
		padding 0 15px 2px
		margin-left 10px
		cursor pointer
		border-radius 6px
		width 200px
		z-index 2
		position relative
		&:first-child
			margin-left 0
	&-bg
		width 200px
		position absolute
		z-index 1
		left 0
		margin-left 6px
		background-color #ddd
		box-shadow 0 0 6px 0 rgba(#000, .1)
		border-radius 6px
		height 28px
		transition left .3s ease-in-out
.main
	display flex
	justify-content flex-start
	flex-wrap wrap
	align-items flex-start

</style>
